/* Homepage layout */

body {
    font-family: Arial, sans-serif;
}

.hero {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
}

.journal {
    font-size: 2rem;
    font-weight: 600;
    margin: 0;
}

.buttonsHero {
    display: flex;
    gap: 12px;
}

.addNewTradeButton,
.showGraph {
    background-color: #4CAF50;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1rem;
    font-weight: 600;
    text-transform: uppercase;
}

.addNewTradeButton:hover,
.showGraph:hover {
    background-color: #45a049;
}